<template>
    <div id="message">
        <div class="tittle">
            <h1>留言板</h1>
            <h3>已经有{{messageCount}}条留言</h3>
        </div>
        <MessagePlan />
    </div>
</template>

<script>
    import MessagePlan from "../../components/message/MessagePlan";
    import { getMassageData } from "../../service/api";

    export default {
        name: "Message",
        data() {
            return {
                messageCount: 0,
                comments: []
            };
        },
        created() {
            //请求网络数据
            getMassageData()
                .then(response => {
                    this.comments = response.data;
                    this.messageCount = this.comments.length;
                })
                .catch(error => {
                    console.log(error);
                });
        },
        components: {
            MessagePlan
        }
    };
</script>

<style lang="scss" scoped>
    @import "../../style/_statement.scss";

    #message {
        font-size: 16px;
    }

    .tittle {
        display: flex;
        align-items: center;
        flex-direction: column;
        margin: 60px 0;
    }

    .tittle h1 {
        font-size: 50px;
    }

    .tittle h3 {
        margin-top: 20px;
        font-size: 30px;
    }

    @include styleAllPhone {
        #message {
            font-size: conversionPX(16);
        }
        .tittle h3 {
            margin-top: conversionPX(10);
            font-size: conversionPX(15);
        }
        .tittle {
            margin: conversionPX(20) 0;
        }
        .tittle h1 {
            font-size: conversionPX(20);
        }
    }
</style>